import React from 'react'
import BuildControl from './BuildControl/BuildControl'
import classes from './BuildControls.css'

const controls = [
    {label:'Meat', type:'meat'},
    {label:'Cheese', type:'cheese'},
    {label:'Bacon', type:'bacon'},
    {label:'Salad', type:'salad'}
]

const buildControls = (props) =>{
    return(
        <div className={classes.BuildControls}>
            <p>Current Price : <strong>{props.price.toFixed(2)}</strong></p>
            {
                controls.map(el => 
                    <BuildControl 
                        key={el.label} 
                        label={el.label} 
                        added={() => props.ingredientsAdded(el.type)}
                        removed={() => props.ingredientsRemoved(el.type)}
                        disabled={props.disabled[el.type]}/>)
            }
            <button className={classes.OrderButton} onClick={props.ordered} disabled={!props.purchasable}>{props.isAuth ? 'ORDER NOW' : 'SIGN UP TO ORDER' }</button>
        </div>
    )
}

export default buildControls


