import React,{ Component } from "react";
import {connect} from 'react-redux'
import Aux from '../../hoc/Auxiliary/Auxiliary'
import Burger from '../../components/Burger/Burger'
import BuildControls from '../../components/Burger/BulidControls/BulidControls'
import Modal from '../../components/UI/Modal/Modal'
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary'
import Spinner from '../../components/UI/Spinner/Spinner'
import WithErrorHandler from '../../hoc/withErrorHandler/withErrorHandler'
import axios from '../../axios-order'
import * as actions from '../../store/action/index'
import spinner from "../../components/UI/Spinner/Spinner";

const INGREDIENTS_PRICE = {
    salad : 0.3,
    bacon : 0.4,
    cheese : 1.3,
    meat : 0.7
};

export class BurgerBuilder extends Component {

    state = {
        purchasing : false,
    }

    

    componentDidMount(){
      this.props.onInitIngredients();
    }

    updatePurchasable (ingredients) {
        const sum = Object.keys(ingredients)
            .map(egKey => {
                return ingredients[egKey];
            })
            .reduce((sum,el) => {
                return sum + el;
            },0)
        return sum > 0
    }

    addIngredientsHandler = (type) => {
        const oldCount = this.state.ingredients[type];
        const updateCount = oldCount + 1;
        const updateIngredients = {
            ...this.state.ingredients
        };
        updateIngredients[type] = updateCount;
        const priceAddition =  INGREDIENTS_PRICE[type];
        const newPrice = this.state.totalPrice + priceAddition;
        this.setState({ingredients : updateIngredients, totalPrice : newPrice})
        this.updatePurchasable(updateIngredients);
    }

   
    removeIngredients = (type) => {
        const oldCount = this.state.ingredients[type];
        if (oldCount <= 0){
            return
        }
        const updateCount = oldCount - 1;
        const updateIngredients = {
            ...this.state.ingredients
        };
        updateIngredients[type] = updateCount;
        const priceDeduction =  INGREDIENTS_PRICE[type];
        const newPrice = this.state.totalPrice - priceDeduction;
        this.setState({ingredients : updateIngredients, totalPrice : newPrice})
        this.updatePurchasable(updateIngredients);
    }

    purchaseHandler = () => {
        if(this.props.isAuthtenticate) {
            this.setState({purchasing : true})
        } else {
            this.props.onSetReDirectPath('/checkout')
            this.props.history.push('/auth')
        }
       
    }

    purchaseCancelHandler = () => {
        this.setState({purchasing : false})
    }

    purchaseContinueHandler = () => {
        this.props.onInitPurchase()
        this.props.history.push('/checkout')
    }
    render(){
        const disableInfo ={
            ...this.props.ings
        }
        for(let key in disableInfo){
            disableInfo[key] = disableInfo[key] <= 0;
        }

        let burger = this.props.error ? <p>INGREDIENT CAN'T LOAD DATA</p> : <Spinner />
        let orderSummary = null

        if(this.props.ings){
            burger = (
                <Aux>
                    <Burger ingredients={this.props.ings} />
                    <BuildControls 
                    ingredientsAdded={this.props.onIngredientsAdded} 
                    ingredientsRemoved={this.props.onIngredientsRemoved}
                    disabled ={disableInfo}
                    price={this.props.price}
                    isAuth = {this.props.isAuthtenticate}
                    purchasable={this.updatePurchasable(this.props.ings)}
                    ordered={this.purchaseHandler}/>
                </Aux>
            );

            orderSummary =  <OrderSummary 
            ingredients={this.props.ings} 
            purchaseCancel={this.purchaseCancelHandler} 
            purchaseContinue={this.purchaseContinueHandler}
            price={this.props.price}/>
        }

        if(this.state.loading){
            orderSummary = <Spinner />
        }
        return(
            <Aux>
                <Modal show={this.state.purchasing} cancelOrder={this.purchaseCancelHandler}>
                   {orderSummary}
                </Modal>
                {burger}
            </Aux>
        )
    }
}

const mapStateToProps = state => {
    return {
        ings : state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        error: state.burgerBuilder.error,
        isAuthtenticate : state.auth.tokenId !== null
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onIngredientsAdded:(ingName) => dispatch(actions.addIngredient(ingName)),
        onIngredientsRemoved:(ingName) => dispatch(actions.removeIngredient(ingName)),
        onInitIngredients:() => dispatch(actions.initIngredients()),
        onInitPurchase:() => dispatch(actions.purchaseInit()),
        onSetReDirectPath:(path) => dispatch(actions.setAuthRedirectPath(path))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(WithErrorHandler(BurgerBuilder,axios))


