import React from 'react';
import classes from './Logo.css'
import BurgerLogo from '../../assets/image/burger-logo.png'

const logo = () => {
    return (
        <div className={classes.Logo}>
            <img src={BurgerLogo} alt="My BUrger App" />
        </div>
    )
}

export default logo


