import * as actionTypes from '../action/actionTypes'
import {updateObject} from '../../shared/Utility'

const intialState = {
    ingredients: null,
    totalPrice : 4,
    error : false,
    building: false
}

const INGREDIENTS_PRICE = {
    salad : 0.3,
    bacon : 0.4,
    cheese : 1.3,
    meat : 0.7
};

const addIngredient = (state, action) => {
    const updateIngredient = {[action.ingredientsName] : state.ingredients[action.ingredientsName] + 1}
    const updateIngredients = updateObject(state.ingredients,updateIngredient)
    const updateState = {
        ingredients:updateIngredients,
        totalPrice : state.totalPrice + INGREDIENTS_PRICE[action.ingredientsName],
        building: true
    }
    return updateObject(state,updateState)
}

const removeIngredient = (state, action) => {
    const updateIngt = {[action.ingredientsName] : state.ingredients[action.ingredientsName] - 1}
    const updateIngts = updateObject(state.ingredients,updateIngt)
    const updateSt = {
        ingredients:updateIngts,
        totalPrice : state.totalPrice - INGREDIENTS_PRICE[action.ingredientsName],
        building: true
    }
    return updateObject(state,updateSt)
}

const setIngredients = (state, action) => {
    return updateObject(state,{ingredients : {
                    salad : action.ingredients.salad,
                    bacon : action.ingredients.bacon,
                    cheese : action.ingredients.cheese,
                    meat : action.ingredients.meat
                },
                totalPrice : 4,
                error : false,
                building: false})
}

const fetchIngredientsFailed = (state, action) => {
    return updateObject(state,{error : true})
}
const reducer = (state =intialState,action) => {
    switch(action.type) {
        case actionTypes.ADD_INGREDIENT: return addIngredient(state, action)         
        case actionTypes.REMOVE_INGREDIENT:return removeIngredient(state, action)    
        case actionTypes.SET_INGREDIENTS:return setIngredients(state, action)
        case actionTypes.FETCH_INGREDIENTS_FAILED:return fetchIngredientsFailed(state, action)
        default:return state;
    }
}

export default reducer


